.hero {
    height: 100vh;
    background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
    display: flex;
}

.hero-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* gap: 20px; */
    padding-left: 150px;
    line-height: 1.1;
    padding-top: 90px;
}

.hero-left h2 {
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}

.hero-left p {
    color: #171717;
    font-size: 100px;
    font-weight: 700;
}

.hero-hand-icon {
    display: flex;
    align-items: center;
    gap: 20px;
}

.hero-hand-icon img {
    width: 80px;
}

.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 275px;
    height: 50px;
    border-radius: 75px;
    margin-top: 30px;
    background: #fb1c1c;
    color: white;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.hero-latest-btn img{
    width: 35px;
}

.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-right img{
    width: 360px;
}