.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 80px;
}

.new-collections h1{
    color: #171717;
    font-size: 35px;
    font-weight: 600;
    margin-top: 50px;
}

.new-collections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 35px 76px;
    row-gap: 60px;
}
